import React, {PropTypes} from 'react';
import Header from './common/header/ManageHeaderPage';

class App extends React.Component {
  render() {
    return (
        <div>
          <Header/>
            {this.props.children}
        </div>
    );
  }
}
App.propTypes = {
  children: PropTypes.object.isRequired
};

export default App;



